// Content.jsx
import React from "react";
import { Col, Container, Row } from "react-bootstrap";
import studying from "../img/studying.png"; // Asegrate de que la ruta de la imagen sea correcta
import styles from "../styles/Content.module.css";

function Content() {
  return (
    <Container className={styles["container-content"]}>
      <Row className="d-flex flex-column p-1">
        <Col
          sm={12}
          className={`${styles["title-container"]} text-white p-3 rounded`}
        >
          <h1>CMO ENTENDER LA PROGRAMACIN</h1>
        </Col>
        <Col sm={12}>
          <p className={`${styles["custom-container"]} text-white p-3 rounded`}>
            Quizs eres un programador noble que est comenzando a desenvolverse
            en algn lenguaje de programacin y te est constando arrancar o
            afianzar todos los conceptos nuevos que te vas encontrando. El
            aprendizaje de un lenguaje de programacin es algo que requiere un
            relativo alto nmero de horas, por lo que cunto mayor sea la
            calidad de ese tiempo que dediquemos mejor sern los resultados.
          </p>
          <p className={`${styles["custom-container"]} text-white p-3 rounded`}>
            A continuacin, os expongo una serie de tips o consejos (como
            queris llamarlos) inspirados en experiencias propias y unos cuantos
            posts ledos sobre el tema que rozan la temtica de la auto ayuda
            encapsulada en recetas.
          </p>
        </Col>
        <Col
          sm={12}
          md={6}
          className="img-container d-flex justify-content-center"
        >
          {/* Asegrate de que la ruta de la imagen sea correcta y que studying.png exista */}
          <img
            alt="Estudio"
            className={`${styles["studying-img"]} img-fluid`}
            src={studying}
          />
        </Col>
        <Col sm={12} md={6} className="text-white">
          <h3>Pasos a seguir:</h3>
          <ul className={`${styles["custom-list"]} list-group`}>
            <li className="list-group-item bg-primary text-white">
              Aprende los fundamentos de programacin
            </li>
            <li className="list-group-item bg-primary text-white">
              Practica todos los das lo aprendido
            </li>
            <li className="list-group-item bg-primary text-white">
              Construye tu primer proyecto de programacin
            </li>
            <li className="list-group-item bg-primary text-white">
              Elige lo que te interesa.
            </li>
            <li className="list-group-item bg-primary text-white">
              Comienza con un cdigo sencillo
            </li>
            <li className="list-group-item bg-primary text-white">
              Construye algo til
            </li>
            <li className="list-group-item bg-primary text-white">
              Resuelve errores o bugs en tu cdigo
            </li>
          </ul>
        </Col>
      </Row>
    </Container>
  );
}

export default Content;
